import styles from './Node.module.css';

export function Node({ active, value, description, dots = [], line = {} }) {
    return (
        <div className={styles.wrapper}>
            <div className={`${styles.circle} ${active ? styles.circleActive: ''}`}>
                {dots.map((dot, index) => <span key={index} style={dot} className={styles.dot} />)}
                <div className={styles.line} style={line} />
                <div className={styles.waves}></div>
            </div>
            <p className={styles.title}>{value}</p>
            <p className={styles.description}>
                <span>{value} -</span> <span>{description}</span>
            </p>
        </div>
    )
}