const partners = [
    'Dino Swap',
    'Polygon Punks',
    'Polygon Ecosystem',
    'Dino Swap 2',
    'Polygon Punks 2',
    'Polygon Ecosystem 2',
];

const evenPartners = partners.length % 2 === 0;

export let partnerGroups = [];

if (evenPartners) {
    const itemCountInRow = partners.length / 2;
    partnerGroups.push(partners.slice(0, itemCountInRow))
    partnerGroups.push(partners.slice(itemCountInRow))
} else {
    const itemCountInFirstRow = Math.floor(partners.length / 2);
    partnerGroups.push(partners.slice(0, itemCountInFirstRow))
    partnerGroups.push(partners.slice(itemCountInFirstRow))
}
