import { useState, useCallback, useEffect, useRef } from 'react';
import {
  Main,
  Promo,
  RoadMap,
  Partners,
  Team,
  Movie,
  Footer,
} from '@sections';
import { FullPage, Slide } from 'react-full-page';
import styles from './App.module.css';
import './animation.css';

function showAnimation() {
  const div = document.createElement('div');
  div.classList.add('lalala');
  document.body.append(div);
}

export function App() {
  const ref = useRef();
  const [scrollMode, setScrollMode] = useState('full-page');

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setScrollMode('full-page');
        }
      },
      {
        root: null,
        rootMargin: "0px",
        threshold: 0.1
      }
    );
    if (ref.current) {
      observer.observe(ref.current);
    }
  }, [ref]);

  const handleBeforeChange = useCallback((params) => {
    if (params.from === 0) {
      showAnimation();
    }
  }, []);

  const handleAfterChange = useCallback((params) => {
    if (params.from === 0) {
      const div = document.querySelector('.lalala');
      div.remove();
    }

    if (params.to === 2) {
      setScrollMode('normal');
    }
  }, [setScrollMode]);

  return (
    <div>
      <FullPage
        scrollMode={scrollMode}
        beforeChange={handleBeforeChange}
        afterChange={handleAfterChange}
        duration={1000}
      >
        <Slide>
          <div className={styles.slide}>
            <Main />
          </div>
        </Slide>
        <Slide>
          <div ref={ref} className={styles.slide}>
            <Promo />
          </div>
        </Slide>
        <Slide>
          <RoadMap />
          <Partners />
          <Team />
          <Movie />
          <Footer />
        </Slide>
      </FullPage>
    </div>
  )
}
